@import "ScssHelpers/_helpers_all.scss";

.breadcrumbs__list {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	margin: 0 -0.8rem -1rem 0;

	@include bp($point_4 - 1) {
		overflow-x: scroll;
	}

	&::-webkit-scrollbar {
		width: 0;
	}
}

.breadcrumbs__item {
	@extend %global_flex_block_row_nowrap_flex-start_center;
	flex-shrink: 0;

	padding: 0 0.8rem 1rem 0;

	&:not(:last-child) {
		color: $green_2;

		&::after {
			content: "";
			width: 3px;
			height: 3px;
			border-radius: 50%;
			margin-left: 1rem;

			background-color: $green_2;
		}
	}

	&:last-child {
		color: $gray_3;

		.breadcrumbs__link {
			&:hover {
				cursor: default;
			}
		}
	}
}

.breadcrumbs__link {
	@extend %transition_color;

	font-weight: 400;
	font-size: 1rem;
	line-height: 1.4;
	color: inherit;

	// &.breadcrumbs__link--active_mod {
	// 	color: $gray_3;
	// }

	&:focus,
	&:hover {
		text-decoration: none;
		color: $gray;
	}
}
