@import 'ScssHelpers/_helpers_all.scss';
@import 'ScssHelpers/extends/forms/_form_default_input.scss';

.account_wishlist{
	@include flex_block(column, nowrap, flex-start, stretch);
	@include bp($point_2, min) {
		padding: 1.5rem 0 0 0;
	}
	
	@include bp($point_3, $point_5) {
		padding: 1rem 4.5rem 0 0;
	}

	@include bp($point_5 - 1) {
		padding: 0;
	}
}

.account_wishlist__item {
	position: relative;
	
	@include bp($point_5, min) {
		padding: 4.6rem 0 4.6rem 5rem;
	}

	@include bp($point_5 - 1) {
		padding: 4.6rem 0 4.6rem 0;
	}
	
	&:not(:last-child) {
		&::after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 200vw;
			height: 1px;
			background-color: $gray_2;

			@include bp($point_5 - 1) {
				left: -1.5rem;
			}
		}
	}

	&:first-child {
		@include bp($point_5 - 1) {
			padding-top: 2rem;
		}
	}

	&:last-child {
		@include bp($point_5 - 1) {
			padding-bottom: 0;
		}
	}
}

.account_wishlist__card {
	position: relative;
	width: 100%;
	padding: 3rem 0 0 0;

	@include flex_block(row, wrap, flex-start, flex-start);
	
	@include bp($point_5, min) {
		max-width: 38rem;
	}
}


.account_wishlist_empty {
	@include bp($point_2, min) {
		padding: 6rem 5rem;
	}

	@include bp($point_3, $point_5) {
		padding: 6rem 4rem;
	}
}

.account_wishlist_empty__text {
	margin: 0 0 2.8rem 0;
	
	@include bp($point_5, min) {
		max-width: 41rem;
	}

	@include bp($point_5 - 1) {
		text-align: center;
	}

	p {
		@extend %text;
		color: $green;

		&:not(:last-child) {
			margin: 0 0 2.3rem 0;
		}
	}
}

.account_wishlist_empty__btn {
	@extend %transition_opacity;
	@extend %dark_btn_1;
	display: inline-flex;
	justify-content: center;

	width: 100%;
	padding: 1.6rem 2rem;
	height: 5rem;
	
	font-size: 1.6rem;
	font-weight: 800;
	font-family: $font_1;
	text-align: center;
	
	cursor: pointer;
	
	@include bp($point_5, min) {
		max-width: 25rem;
	}

	&:hover {
		opacity: .7;
	}
}