@import 'ScssHelpers/_helpers_all.scss';

@import './gender';

.filter_mob {
	font-weight: 300;

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		position: fixed;
		top: 0;
		right: 0;
		z-index: 110;

		width: 100%;
		// height: var(--full-screen);
		height: 100%;

		background: #0000003d;

		transition: opacity 300ms ease-in;
		will-change: opacity;

		&:not(.active_state) {
			opacity: 0;
			pointer-events: none;
		}
	}
}

.filter_mob__elements {
	@extend %transition_opacity;

	max-height: 0;
	opacity: 0;
	pointer-events: none;

	&.filter_mob__elements--active_state {
		opacity: 1;
		pointer-events: initial;
	}
}

// ---------------------------------------------- filter_mob__in
.filter_mob__in {
	position: relative;

	@include bp($point_3) {
		@include flex_block(column, wrap, flex-start);

		width: 100%;
		height: 100%;
		margin-left: auto;

		background: $white;

		transition: transform 300ms ease-in;
		will-change: transform;

		&:not(.active_state &) {
			transform: translateX(100%);
		}

	}

	@include bp($point_6, min) {
		max-width: 40rem;
	}
}

// ---------------------------------------------- form
.filter_mob__form {
	position: relative;

	width: 100%;
	height: 100%;
}

// ---------------------------------------------- header
.filter_mob__header {
	@extend %global_flex_block_row_wrap_center_center;
	@extend %sidebar_padding;

	position: absolute;
	z-index: 2;

	width: 100%;
	min-height: 10rem;
	padding-top: 2rem;
	padding-bottom: 2rem;

	background-color: $white;
	border-bottom: 1px solid $gray_2;
}

.filter_mob__header_title {
	font-size: 1.8rem;
	font-weight: 600;

	max-width: calc(100% - 5rem);
	padding: 0 1rem;

	text-align: center;
}

.filter_mob__middle {
	@extend %sidebar_padding;

	position: absolute;
	width: 100%;
	height: 100%;

	padding-bottom: 2rem;

	overflow-x: hidden;
	overflow-y: auto;

	@include bp($point_6, min) {
		padding-top: max(33.3vh, 22.8rem);
	}

	@include bp($point_6 - 1) {
		padding-top: 11.8rem;
	}
}

.filter_mob__header_btn {
	@extend %transition_opacity;

	flex: 0 0 auto;

	cursor: pointer;

	&:first-child {
		width: 2.1rem;
		height: 1.4rem;
		margin-right: auto;

		@include bp($point_6 - 1) {
			opacity: 0;
			pointer-events: none;
		}
	}

	&:last-child {
		width: 2rem;
		height: 2rem;
		margin-left: auto;
	}

	&:hover {
		opacity: .5;
	}
}

// ---------------------------------------------- accordion
.filter_mob__element {
	padding: 2.3rem 0;
	border-top: 1px solid #eae9e8;
}