@import "ScssHelpers/_helpers_all.scss";

.card {
	@include flex_block(column, nowrap, flex-start, center);
}

.card__wrap {
	position: relative;
	width: 100%;

	@include bp($point_2, min) {
		padding-bottom: 120.4%;
	}

	@include bp($point_3, $point_4) {
		padding-bottom: 130.4%;
	}

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 1.5rem;
		}
	}

	@include bp($point_4 - 1) {
		padding-bottom: 133.4%;
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.card__img {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.card__sale {
	position: absolute;
	top: 1rem;
	right: 1rem;

	span {
		padding: 2px;

		font-weight: 500;

		line-height: 1.35;
		color: $white;
		text-align: center;

		border-radius: 50%;
		text-transform: capitalize;
		background-color: $orange;

		@include flex_block(row, nowrap, center, center);

		@include bp($point_5, min) {
			width: 4.5rem;
			height: 4.5rem;

			font-size: 1.4rem;
		}

		@include bp($point_5 - 1) {
			width: 3.5rem;
			height: 3.5rem;

			font-size: 1.2rem;
		}
	}

	@include flex_block(row, nowrap, center, center);
}

.card__like {
	@extend %transition_color;

	position: absolute;
	z-index: 5;
	bottom: 1rem;
	right: 1rem;

	color: $green_2;

	background-color: $white;
	border-radius: 50%;
	cursor: pointer;

	@include flex_block(row, nowrap, center, center);

	@include bp($point_5, min) {
		width: 4.5rem;
		height: 4.5rem;
		padding: 1.5rem 1.3rem 1.4rem 1.3rem;
	}

	@include bp($point_5 - 1) {
		width: 3.5rem;
		height: 3.5rem;
		padding: 1.1rem 1rem;
	}
	&:focus,
	&:hover {
		.icon {
			color: $orange;
		}
	}
}

.icon {
	.card__like & {
		@extend %transition_color;
	}
}

.card__title {
	@extend %title_card;
	@extend %transition_color;

	&:focus,
	&:hover {
		text-decoration: none;
		color: $gray_3;
	}
}

.card__category {
	@extend %text_card;
	@extend %transition_color;

	padding-left: 4px;

	&:focus,
	&:hover {
		text-decoration: none;
		color: $gray_3;
	}

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 0.7rem;
		}
	}
}

.card__prices_in {
	@extend %global_flex_block_row_wrap_center_center;
}

.card__price {
	@extend %price_card;

	font-weight: 500;

	&.card__price--normal_mod {
		color: $green_2;
	}

	&.card__price--old_mod {
		position: relative;

		color: $gray_4;

		&:not(:last-child) {
			margin-right: 0.55rem;
		}

		&::after {
			content: "";
			position: absolute;
			top: 0.9rem;
			left: -1px;

			width: 104%;
			height: 2px;

			background-color: $gray_4;
		}
	}
	&.card__price--new_mod {
		color: $red;
	}
}
