@import "ScssHelpers/_helpers_all.scss";
@import 'ScssHelpers/extends/forms/_form_default_input.scss';
@import 'ScssHelpers/extends/forms/_form_default_select.scss';

.account {
	display: flex;
	flex-direction: column;
	flex: 1 1 auto;

	padding-bottom: 0;
	
	@include bp($point_4 - 1) {
		border-bottom: 1px solid $gray_2;
	}

	@include bp($point_3) {
		padding-left: 0;
		padding-right: 0;
	}
	
	@include bp($point_2, min) {
		padding-top: 4.1rem;
	}

	@include bp($point_3, $point_5) {
		padding-top: 4.3rem;
	}

	@include bp($point_5 - 1) {
		padding-top: 4.7rem;
	}
}

.account_in {
	display: flex;
	height: 100%;
	
	
	@include bp($point_2, min) {
		flex: 1 0 auto;
		max-width: 84.4rem;
	}

	@include bp($point_3) {
		max-width: 100%;
	}

	@include bp($point_5 - 1) {
		padding: 2.2rem 1.5rem 4rem;
	}
}

.account__title {
	@extend %title_account;

	position: relative;

	
	@include bp($point_2, min) {
		padding-bottom: 2.7rem;
	}

	@include bp($point_3, $point_5) {
		padding-bottom: 3.5rem;
	}

	@include bp($point_5 - 1) {
		padding-bottom: 3rem;
	}

	&::before {
		content: '';
		position: absolute;
		bottom: 0;
		left: calc(-1 * var(--gl-indent));
		width: calc(100% + 2 * var(--gl-indent));
		height: 1px;
		background-color: $gray_2;
	}
}

.account__wrapper {
	width: 100%;
	@include bp($point_2, min) {
		min-height: 45rem;
	}
	
	@include bp($point_3, $point_5) {
		min-height: 59rem;
	}
	
	@include bp($point_5, min) {
		@include flex_block(row, nowrap, flex-start, stretch);
	}
}

.account__content {
	width: 100%;
}

.account_form {
	width: 100%;

	@include bp($point_2, min) {
		padding: 5.4rem 0 6rem 5rem;
		max-width: 69rem;
	}

	@include bp($point_3, $point_5) {
		padding: 5.3rem 0 5.3rem 4rem;
		max-width: 35rem;
	}
	
}

.account_form__fieldlist {
	width: 100%;

	@include bp($point_2, min) {
		margin: 0 -2rem -2rem 0;
	}

	@include bp($point_3, $point_5) {
		margin: 0;
	}
	&.account_form__fieldlist--row_mod {
		@include flex_block(row, wrap, flex-start, stretch);
		
	}

	&.account_form__fieldlist--column_mod {
		@include flex_block(column, nowrap, flex-start, stretch);
	}
}

.account_form__row {
	@include flex_block(row, nowrap, flex-start, stretch);

	margin: 0 0 -2rem -0;
}

.account_form__input_w {
	@include bp($point_2, min) {
		padding: 0 1.9rem 1.4rem 0;
	}

	@include bp($point_3) {
		padding: 0;
	}

	@include bp($point_5) {
		width: 100%;
	}

	&:not(:last-child):not(.account_form__input_w--small_mod) {
		@include bp($point_3, $point_5) {
			margin: 0 0 1.7rem 0;
		}

		@include bp($point_5 - 1) {
			margin: 0 0 1.5rem 0;
		}
	}

	&.account_form__input_w--small_mod {
		padding: 0 1rem 1.5rem 0;
	}
}

.account_form__field {
	@include bp($point_5, min) {
		width: 25rem;
	}

	@include bp($point_5 - 1) {
		width: 100%;
	}

	&.account_form__field--small_mod {
		width: 12rem;
	}

	&.account_form__field--select_mod {
		position: relative;

		&::after {
			content: '';
			position: absolute;
			top: 46%;
			right: 0.8rem;
			margin: 0 0 0 auto;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: .5rem .3rem 0 .3rem;
			border-color: $green_2 transparent transparent transparent;
			transform: translateY(-50%);
			pointer-events: none;
		}
	}
}

.account_form__input {
	@extend %form_element_card;
	-moz-appearance:textfield;

	&::-webkit-outer-spin-button,
	&::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
	}
}

.account_form__select {
	@extend %form_select_default;
	background-color: transparent;
	cursor: pointer;
	-webkit-appearance: none;
  -moz-appearance: none;
	-o-appearance: none;
  appearance: none;
}

.account_form__btn {
	@extend %transition_opacity;
	@extend %dark_btn_1;
	margin: 3.5rem 0 0 0;
	width: 100%;
	height: 5rem;
	padding: 1.4rem 2rem;

	display: inline-flex;
	justify-content: center;

	
	font-size: 1.6rem;
	font-weight: 800;
	font-family: $font_1;
	text-align: center;
	
	cursor: pointer;
	
	@include bp($point_5, min) {
		max-width: 25rem;
	}

	&:hover {
		opacity: .7;
	}

	&.account_form__btn--hidden_state {
		display: none;
	}
}