.slider_product {
	display: flex;
	align-items: flex-start;
	min-width: 0;
	width: 100%;
}
.slider_product__col {
	display: flex;
	flex-direction: column;
	min-width: 0;
	flex-shrink: 0;

	width: 20%;
	height: 34rem;
	padding: 0 1rem 0 0;
}
.slider_product_thumbs .slider_product_thumbs__image {
	transition: 0.25s;
	-webkit-filter: grayscale(100%);
	filter: grayscale(100%);
	opacity: 0.8;
}
.slider_product_thumbs .slider_product_thumbs__image:hover {
	opacity: 1;
}
.slider_product_thumbs .swiper-slide-thumb-active .slider_product_thumbs__image {
	-webkit-filter: grayscale(0%);
	filter: grayscale(0%);
	opacity: 1;
}
.slider_product_images {
	width: 80%;
	max-width: 43rem;
}

.slider_product_thumbs__image,
.slider_product_images__image {
	width: 100%;
	height: 100%;
	overflow: hidden;
}
.slider_product_thumbs__image img,
.slider_product_images__image img {
	display: block;
	width: 100%;
	height: 100%;
	-o-object-fit: cover;
	object-fit: cover;
}

@media (max-width: 1023px) {

	.slider_product__col {
		padding: 0;
		height: 52rem;
	}
}

@media (max-width: 767.98px) {
	.slider_product {
		flex-direction: column-reverse;
		width: 100%;
		margin: 0;
	}
	.slider_product__col {
		flex-direction: row;
		align-items: center;
		margin-right: 0;
		margin-top: 24px;
		width: 100%;
		height: 100%;
	}
	.slider_product_images__images {
		width: 100%;
	}
	.slider_product_thumbs {
		height: 15rem;
		width: 100%;
	}
}
