@import 'ScssHelpers/_helpers_all.scss';

.about {
	background-color: $green;
	overflow: hidden;

	@include bp($point_2, min) {
		padding: 4.4rem 3.4rem 3.8rem;

		&::before {
			height: 12.5rem;
		}
	}

	@include bp($point_3, $point_4) {
		padding: 14rem 3.4rem 3.8rem;

		&::before {
			height: 21.8rem;
		}

		&::after {
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			z-index: 1;

			width: 2.9rem;

			background: $white;
			content: '';
		}
	}

	@include bp($point_4, min) {
		&::before {
			position: absolute;
			bottom: 0;
			left: 0;

			width: 100%;

			background-color: $white;
			content: '';
		}
	}

	@include bp($point_4 - 1) {
		padding: 7rem 3.4rem 7.2rem;
	}
}

.about__in {
	@include bp($point_2, min) {
		position: relative;
		max-width: 104.8rem;
	}

	@include bp($point_3) {}
}

// ---------------------------------------------- row
.about__row {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start);
	}

	@include bp($point_3) {}
}

// ---------------------------------------------- column
.about__col {
	@include bp($point_2, min) {
		&:first-child {
			width: 54.77%;
			padding: 8.8rem 7.6rem 13rem 0;
		}

		&:nth-child(2) {
			width: 45.23%;
		}
	}

	@include bp($point_3, $point_4) {
		&:first-child {
			position: relative;
			z-index: 1;

			width: 100%;
			max-width: 50rem;
			margin: 0 auto;
			padding-bottom: 5rem;

			&::after {
				position: absolute;
				top: 0;
				left: 50%;
				z-index: -1;

				width: 100vw;
				height: 100%;

				background-color: $green;
				content: "";
				transform: translateX(-50%);
			}
		}

		&:nth-child(2) {
			width: 100%;
			max-width: 22.6rem;
			margin: 0 calc(8.6rem - var(--gl-indent)) 0 auto;
		}
	}

	@include bp($point_4 - 1) {
		&:nth-child(2) {
			display: none;
		}
	}
}

// ---------------------------------------------- decor
.about__decor {
	position: absolute;
	top: 0;

	@include bp($point_2, min) {
		left: 50%;

		margin-top: -9.8rem;
		margin-left: -4.8rem;
	}

	@include bp($point_3, $point_4) {
		right: 0;

		margin-top: -1.4rem;
		margin-right: 15.7rem;
	}

	@include bp($point_4 - 1) {
		right: 0;

		margin-top: -2rem;
		margin-right: 3.2rem;
	}
}

.about__decor_icon {
	color: $white;

	@include bp($point_2, min) {
		width: 18rem;
		height: 26.4rem;
	}

	@include bp($point_3, $point_4) {
		width: 10.5rem;
		height: 15.4rem;
	}

	@include bp($point_4 - 1) {
		width: 10rem;
		height: 14.6rem;
	}
}

// ---------------------------------------------- title
.about__title {
	font-family: $font_2;
	font-weight: 400;
	text-transform: uppercase;
	line-height: 0.9;
	color: $gray_2;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}

	strong {
		display: block;


		font-family: $font_2;
		font-weight: 400;
		text-transform: uppercase;
		letter-spacing: normal;

		@include bp($point_4, min) {
			margin-top: .8rem;
		}

		@include bp($point_4 - 1) {
			margin-top: .4rem;
		}
	}

	@include bp($point_4, min) {
		font-size: 3rem;

		strong {
			font-size: 9rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 2rem;

		strong {
			font-size: 5rem;
		}
	}
}

.about__description {
	font-size: 1.4rem;
	line-height: 1.36;

	color: $white;

	@include bp($point_2, min) {}

	@include bp($point_3) {}
}

// ---------------------------------------------- picture
.about__picture_w {
	position: relative;

	display: block;
	padding-top: 114.55%;

	@include bp($point_2, min) {
		width: 100%;
	}

	@include bp($point_3) {
		width: 22.6rem;
		margin: 0 5rem 0 auto;
	}
}

.about__picture {
	position: relative;

	display: block;
	width: 100%;
	padding-top: 114.55%;
}

.about__picture_img {
	@extend %stretch;
	@extend %cover_img;
}
