@import "ScssHelpers/_helpers_all.scss";

.faq {
	@include bp($point_2, min) {
		padding: 0.8rem 1.5rem 10rem 1.5rem;
	}

	@include bp($point_3, $point_6) {
		padding: .8rem 1.5rem 10rem 1.5rem;
	}
	
	@include bp($point_6 - 1) {
		padding: .9rem 1.5rem 7rem 1.5rem;
	}
}

.faq__breadcrumbs_wrap {
	max-width: 134rem;

	margin: 0 auto 2rem;

	@include bp($point_3, $point_4) {
		margin-bottom: 2.4rem;
		padding: 0 0.6rem;
	}

	@include bp($point_4 - 1) {
		min-height: 4rem; // ios safari height fix
		margin-bottom: 0.6rem;
	}
}

.faq__in {
	max-width: 84rem;
}

.faq__wrapper {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start);
	}

	@include bp($point_3) {
		@include flex_block(column, nowrap, flex-start, center);
	}
}

.faq__title {
	font-weight: 500;
	text-transform: uppercase;
	
	color: $green_2;
	
	@include bp($point_2, min) {
		margin-bottom: 3.5rem;

		font-size: 2.5rem;
		letter-spacing: -.02em;
	}
	
	@include bp($point_3) {
		
		font-size: 2rem;
		line-height: 2rem;
		text-align: center;
	}
	
	@include bp($point_3, $point_6) {
		margin: 0 0 2.5rem 0;
	}

	@include bp($point_6 - 1) {
		margin: 0 0 2.9rem 0;
	}
}

.faq_block {
	width: 100%;
	
	@include bp($point_2, min) {
		padding: 0 2rem 0 0;
		max-width: 49rem;
	}

	@include bp($point_3, $point_6) {
		max-width: 47rem;
		margin: 0 0 4.5rem 0;
	}

	@include bp($point_6 - 1) {
		margin: 0 0 3.2rem 0;
	}
}

.faq_block__group {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin: 0 0 4rem 0;
		}

		@include bp($point_3, $point_6) {
			margin: 0 0 5.3rem 0;
		}

		@include bp($point_6 - 1) {
			margin: 0 0 3.2rem 0;
		}
	}
}

.faq_block__title {
	
	font-weight: 800;
	font-size: 1.4rem;
	line-height: 1.9rem;
	text-transform: uppercase;
	
	color: $green_2;

	@include bp($point_2, min) {
		margin: 0 0 1.5rem 0;
	}

	@include bp($point_3) {
		margin: 0 0 2.5rem 0;
	}
}

.faq_block__item {
	
	border-bottom: 1px solid $gray_2;
	
	cursor: pointer;
	
	@include bp($point_2, min) {
		padding: 1.5rem 3rem 1.5rem 0;
	}

	@include bp($point_3, $point_6) {
		padding: 2.5rem 3rem 2.5rem 0;
	}
	
	@include bp($point_6 - 1) {
		padding: 1.5rem 0 1.6rem 0;
	}

	&:first-child {
		border-top: 1px solid $gray_2;
	}
}

.faq_block__question {
	position: relative;

	font-size: 1.4rem;
	line-height: 2.4rem;
	
	color: $green_2;
	
	@include bp($point_2, min) {
		font-weight: 800;
	}

	@include bp($point_3) {
		font-weight: 400;
	}

	@include bp($point_6 - 1) {
		padding: 0 3rem 0 0;
	}

	&::before,
	&::after {
		content: '';
		position: absolute;
		top: 50%;
		
		width: 1rem;
		height: 1px;
		
		background-color: $green_2;

		@include bp($point_6, min) {
			right: -3.1rem;
		}

		@include bp($point_6 - 1) {
			right: 0;
		}
	}
	
	&::before {
		transform: translateY(-50%);
	}

	&::after {
		transform: translateY(-50%) rotate(90deg)
	}

	.faq_block__item--active_mod & {
		&::after {
			transform: translateY(-50%) rotate(0);
		}

		&::before, &::after {
			background-color: $orange;
		}
	}
}

.faq_block__content {
	@extend %transition_all;

	max-height: 0;
	
	line-height: 1.19;
	
	overflow: hidden;

	@include bp($point_2, min) {
		padding: 1.3rem 0 0 0;
	}

	@include bp($point_3, $point_6) {
		padding: 1.1rem 0 0 0;
	}

	@include bp($point_6 - 1) {
		padding: 1.1rem 2rem 0 0;
	}

	br {
		@include bp($point_6, min) {
			display: none;
		}
	}

	span {
		font-size: 1.4rem;
		line-height: 1.5rem;
		font-weight: 400;
		font-family: $font_1;
		
		color: $green_2;

		opacity: 0.7;
	}

	.faq_block__item:not(.faq_block__item--active_mod) & {
		margin-top: -1.4rem;
	}
}

.faq_form {
	flex-shrink: 0;
	padding: 2.8rem 2.5rem 2.9rem;
	
	background-color: $gray_2;
	
	@include bp($point_2, min) {
		margin: 3.1rem 0 0 auto;
	}
	
	@include bp($point_6, min) {
		max-width: 30rem;
	}

	@include bp($point_6 - 1) {
			max-width: 100%;
			width: 100%;
	}
}

.faq_form__form {
	width: 100%;
}

.faq_form__title {
	font-family: $font_1;
	font-weight: 500;
	font-size: 1.8rem;
	line-height: 2.5rem;

	text-transform: uppercase;

	color: $green_2;

	&:not(:last-child) {
		margin-bottom: 2.3rem;
	}
}

.faq_form__input_w {
	&:not(:last-child) {
		margin-bottom: 1.4rem;
	}
}

.faq_form__input {
	width: 100%;
	height: 3rem;
	padding-bottom: .5rem;

	font-family: $font_1;
	font-size: 1.4rem;
	line-height: 2.4rem;

	background-color: transparent;
	
	border: none;
	box-shadow: inset 0 -1px 0 0 $input_border_color;
	background-clip: padding-box;
	appearance: none;

	&::-webkit-input-placeholder {
		color: $green;
		opacity: 0.3;
	}

	&::-moz-placeholder {
		color: $green;
		opacity: 0.3;
	}

	&:-ms-input-placeholder {
		color: $green;
		opacity: 0.3;
	}

	&::placeholder {
		color: $green_2;
		opacity: 0.3;
	}

	&:hover,
	&:focus {
		box-shadow: inset 0 -1px 0 0 $input_border_color_active;
	}
}

.faq_form__select_block {
	margin-bottom: 1.6rem;

	@include flex_block(row, wrap, flex-start, stretch);
}

.faq_form__select_descr {
	font-family: $font_1;
	font-size: 1.4rem;
	line-height: 2.4rem;
	font-weight: 400;
	
	color: $green_2;
	
	opacity: 0.3;
}

.faq_form__select_w {
	max-width: 100%;
}

.faq_form__select_field {
	position: relative;
	margin: 0 0 0 -1rem;

	&::after {
		content: '';
		position: absolute;
		top: 57%;
		right: 0;
		margin: 0 0 0 auto;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: .5rem .3rem 0 .3rem;
		border-color: $green_2 transparent transparent transparent;
		pointer-events: none;
		transform: translateY(-50%);
	}
}

.faq_form__select {
	width: 100%;

	padding: 0 1.5rem 0 1.5rem;

	font-family: $font_1;
	font-size: 1.4rem;
	line-height: 2.4rem;
	color: $green_2;

	background-color: transparent;

	border: none;
	cursor: pointer;
	-webkit-appearance: none;
  -moz-appearance: none;
	-o-appearance: none;
  appearance: none;
}

.faq_form__textarea_w {
	margin: 0 0 .2rem 0;

	@include bp($point_4, min) {
		max-width: 25rem;
	}
}

.faq_form__textarea {
	width: 100%;

	padding: 0.4rem 0.7rem;
	min-height: 10rem;

	font-family: $font_1;
	font-size: 1.4rem;
	line-height: 2.4rem;

	background-color: transparent;
	
	border: 1px solid $green_2;

	&::placeholder {
		font-family: $font_1;
		letter-spacing: .01em;
		font-size: 1.4rem;
	}
}

.faq_form__button {
	@extend %transition_opacity;

	width: 100%;
	padding: 1.5rem 3rem;

	font-size: 1.6rem;
	font-weight: 500;
	font-family: $font_1;
	letter-spacing: .01rem;
	text-align: center;
	text-transform: uppercase;

	color: $white;

	background-color: $orange;

	cursor: pointer;

	&:hover {
		opacity: .7;
	}
}