@import "ScssHelpers/_helpers_all.scss";

.about_info {
	@include bp($point_2, min) {
		padding: 9.6rem var(--gl-indent) 16rem;
	}

	@include bp($point_3, $point_4) {
		padding: 9rem var(--gl-indent);
	}

	@include bp($point_4 - 1) {
		padding: 7rem var(--gl-indent);
	}
}

.about_info__head {
	text-align: center;
	max-width: 62.6rem;
	margin: 0 auto;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 16.6rem;
		}

		@include bp($point_3, $point_4) {
			margin-bottom: 9rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 7rem;
		}
	}
}

.about_info__title {
	text-transform: uppercase;
	font-size: 2rem;
	font-weight: 700;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.4rem;
		}
	}
}

.about_info__text {
	@extend %transition_opacity;

	font-weight: 300;
	font-size: 1.4rem;
	line-height: 1.36;
	letter-spacing: 0.03em;
	opacity: 0.7;

	a:hover,
	a:focus {
		text-decoration: none;
		opacity: 1;
	}
}

.about_info__pictures_wrap {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 11.2rem;
		}

		@include bp($point_3, $point_4) {
			margin-bottom: 8.9rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 8.2rem;
		}
	}

	@include bp($point_2, min) {
		&:nth-child(4n + 2) {
			@include flex_block(row, wrap, flex-start, center);
		}

		&:nth-child(4n + 4) {
			@include flex_block(row, wrap, center, center);
		}
	}

	@include bp($point_3) {
		@include flex_block(row, wrap, center, center);
	}
}

.about_info_img__top {
	position: relative;

	&:nth-child(2n + 1) {
		@include bp($point_2, min) {
			width: min(38.2vw, 55rem);
			height: min(38.2vw, 55rem);

			&:not(:last-child) {
				margin-right: min(13.8vw, 20rem);
			}
		}

		@include bp($point_3, $point_4) {
			width: 34.5rem;
			height: 34.5rem;
			&:not(:last-child) {
				margin-right: 2rem;
			}
		}
	}

	&:nth-child(2n + 2) {
		@include bp($point_2, min) {
			width: min(27.8vw, 40rem);
			height: min(24.3vw, 35rem);
		}

		@include bp($point_3, $point_4) {
			width: 25.5rem;
			height: 22rem;
		}

		@include bp($point_4 - 1) {
			padding: 0 4.5rem;
		}
	}

	@include bp($point_4 - 1) {
		width: 100%;
		height: 100%;

		&:not(:last-child) {
			margin-bottom: 12.2rem;
		}
	}
}

.about_info_img__bottom {
	position: relative;

	&:nth-child(2n + 1) {
		@include bp($point_2, min) {
			width: 41rem;
			height: 41rem;

			&:not(:last-child) {
				margin-right: 14.7rem;
			}
		}

		@include bp($point_3, $point_4) {
			width: 28rem;
			height: 28rem;
			margin-right: 5.4rem;
		}

		@include bp($point_4 - 1) {
			padding: 0 3.2rem;
			margin-bottom: 5.2rem;
		}
	}

	&:nth-child(2n + 2) {
		@include bp($point_2, min) {
			width: 28rem;
			height: 28rem;
		}

		@include bp($point_3, $point_4) {
			width: 19.5rem;
			height: 19.5rem;
		}

		@include bp($point_4 - 1) {
			padding: 0 11.8rem 0 3.2rem;
		}

		&:after {
			position: absolute;

			display: block;
			height: 1px;
			background-color: $gray;

			content: "";

			@include bp($point_2, min) {
				width: 31.9rem;
				bottom: 4rem;
				right: -12.8rem;
				transform: rotate(-39.4deg);
			}

			@include bp($point_3, $point_4) {
				width: 22.3rem;
				bottom: 2.6rem;
				right: -9.4rem;
				transform: rotate(-40deg);
			}

			@include bp($point_4 - 1) {
				width: 22.3rem;
				bottom: 2rem;
				right: 2.6rem;
				transform: rotate(-40deg);
			}
		}
	}

	@include bp($point_4 - 1) {
		width: 100%;
		height: 100%;

		&:not(:last-child) {
			margin-bottom: 12.2rem;
		}
	}
}

.about_info_img__icon {
	z-index: 1;
	position: absolute;
	top: 0;
	right: 0;

	@include bp($point_2, min) {
		width: 24.6rem;
		height: 20.5rem;
		margin-top: -9.5rem;
		margin-right: 5.6rem;
	}

	@include bp($point_3) {
		width: 15.4rem;
		height: 12.8rem;
		margin-top: -5.2rem;
	}

	@include bp($point_3, $point_4) {
		margin-right: 4rem;
	}

	@include bp($point_4 - 1) {
		margin-right: 8.6rem;
	}
}

.about_info_img__in {
	@extend %cover_img;
}

.about_info__content {
	max-width: 62.8rem;

	font-weight: 300;
	line-height: 1.35;
	color: $green_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 10rem;
		}

		@include bp($point_3, $point_4) {
			margin-bottom: 8.8rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 6.8rem;
		}
	}

	a:hover,
	a:focus {
		text-decoration: none;
	}

	h3 {
		// font-weight: 300;
		font-weight: 700;
		text-transform: uppercase;

		@include bp($point_2, min) {
			font-size: 2.5rem;

			&:not(:last-child) {
				margin-bottom: 3rem;
			}
		}

		@include bp($point_3) {
			font-size: 2rem;

			&:not(:last-child) {
				margin-bottom: 2.6rem;
			}
		}
	}

	p {
		font-size: 1.4rem;
		opacity: 0.7;
		letter-spacing: 0.027em;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_2, min) {
		margin-left: 25rem;
	}

	@include bp($point_3) {
		margin: 0 auto;
	}
}
