@import 'ScssHelpers/_helpers_all.scss';

.filter_sort_popup {
	@extend %transition_opacity;

	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	display: flex;

	background: rgba(0, 0, 0, 0.322);

	@include z-index(popup);

	@include bp($point_2, min) {
		display: none;
	}

	&:not(.active_state) {
		opacity: 0;
		pointer-events: none;
	}
}

.filter_sort_popup__in {
	@extend %transition_transform;

	margin-top: auto;
	background: $white;
	width: 100%;

	transform: translateY(100%);

	.active_state & {
		transform: translateY(0);
	}

	@include bp($point_6, min) {
		min-height: 28rem;
	}

	@include bp($point_6 - 1) {
		min-height: 29.5rem;
	}
}

.filter_sort_popup__header {
	@extend %global_flex_block_row_wrap_center_center;

	position: relative;

	width: 100%;
	height: 10rem;
	padding: 1rem var(--gl-indent);

	border-bottom: 1px solid $gray_2;
}

.filter_sort_popup__title {
	font-size: 1.8rem;
	font-weight: 300;
	line-height: 1.7;
	color: $green_2;
}

.filter_sort_popup__close {
	position: absolute;
	top: 50%;
	right: 0;

	width: 2rem;
	height: 2rem;
	margin-right: var(--gl-indent);

	color: $green_2;
	cursor: pointer;

	transform: translateY(-50%);
}

.filter_sort_popup__links {
	@extend %global_flex_block_column_wrap_center_center;

	width: 100%;
	padding: 1rem var(--gl-indent);

	@include bp($point_6, min) {
		min-height: 18rem;
	}

	@include bp($point_6 - 1) {
		min-height: 19.5rem;
	}
}

.filter_sort_popup__link_w {
	&:not(:last-child) {
		padding-bottom: .4rem;
	}
}

.filter_sort_popup__link {
	@extend %text_2;

	line-height: 1.7;
	letter-spacing: .05em;
	color: $green_2;

	&::before {
		content: '';
		display: inline-block;
		width: .5rem;
		height: .5rem;
		border-radius: 50%;
		margin-right: .5rem;
		margin-bottom: .3rem;

		background: transparent;
	}

	&:hover {
		opacity: .6;
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}

	.filter_sort_popup__link--active_mod & {
		color: $orange;

		&::before {
			background: $orange;
		}
	}
}