@import "ScssHelpers/_helpers_all.scss";

.registration {
	@include bp($point_2 , min) {
		padding: 4.7rem var(--gl-indent) 11.8rem;
	}

	@include bp($point_3, $point_4) {
		padding: 17.3rem var(--gl-indent) 18.4rem;
	}

	@include bp($point_4 - 1) {
		padding: 4.5rem var(--gl-indent);

	}
}
.registration__title {
	font-weight: 500;
	text-align: center;
	text-transform: uppercase;

	color: $green_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4rem;
		}

		@include bp($point_3, $point_4) {
			margin-bottom: 4.5rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 3.3rem;
		}
	}

	@include bp($point_2, min) {
		font-size: 2.5rem;
	}

	@include bp($point_3) {
		font-size: 2rem;
		letter-spacing: -.01em;
	}
}

.registration_form {
	max-width: 52.1rem;
	margin: 0 auto;
}

.registration_form__wrap {
	&:not(:last-child) {
		margin-bottom: 1.5rem;
	}
}

.registration_form__wrap_in {
	margin: 0 -2rem -2rem 0;

	@include flex_block(row, wrap, flex-start);
}

.registration_form__input_w {
	position: relative;

	padding: 0 2rem 2rem 0;

	@include bp($point_4, min) {
		width: 50%;
	}
	
	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.registration_form__input {
	width: 100%;
	padding: .3rem 0 1rem 0;
	-webkit-appearence: none;
 	&.registration_form__input--password_mod {
		padding: .3rem 3rem 1rem 0;
	}

	&::placeholder {
		opacity: 1;
		color: $gray_3;
	}
}

.registration_form__button_show_pass {
	position: absolute;

	width: 1.4rem;
	height: 1.4rem;

	cursor: pointer;

	@include bp($point_4, min) {
		right: 2.5rem;
		top: 1rem;
	}

	@include bp($point_4 - 1) {
		right: 2rem;
		top: .6rem;
	}
}

.registration_form__checkbox_block {
	@include bp($point_4, min) {
		margin-bottom: 1.9rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 2.4rem;
	}
}

.registration_form__checkbox_w {
	&:not(:last-child) {
		margin-bottom: 1.1rem;
	}	
	& .form_checkbox__block {
		@include flex_block(row, nowrap, flex-start, center, center);
	}
}
.registration_form__checkbox {
	@include bp($point_2, min) {
		margin: 0 1.5rem 0 0;
	}
	
	@include bp($point_3) {
		margin: 0 .8rem 0 0;
	}
}

.registration_form__checkbox_label {
	@extend %transition_color;

	font-weight: 500;
	font-size: 1.2rem;
	letter-spacing: 0.025em;

	color: $green_2;

	a {
		color: $orange;

		&:hover {
			text-decoration: none;
			color: darken($orange, 10);
		}
	}
}

.registration_form__button_w {
	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, center, center);
	}
}

.registration_form__button {
	@extend %transition_opacity;

	padding: 1.6rem 3rem;

	font-size: 1.6rem;
	font-weight: 500;
	letter-spacing: .01rem;
	text-align: center;
	text-transform: uppercase;

	color: $gray_2;

	background-color: $green_2;

	cursor: pointer;

	&:hover {
		opacity: .7;
	}

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-right: 2.8rem;
		}
	}

	@include bp($point_4 - 1) {
		width: 100%;
		margin-bottom: 3.6rem;
	}
}

.registration_form__log_in {
	font-size: 1.2rem;
	font-weight: 500;
	letter-spacing: .026em;
	color: $green_2;

	@include bp($point_4 - 1) {
		text-align: center;
	}

	span {
		&:not(:last-child) {
			margin-right: .5rem;
		}
	}

	a {
		color: $orange;

		&:hover {
			text-decoration: none;
			color: darken($orange, 10);
		}
	}
}
