.gender {
	width: 100%;
}

.gender__list {
	@extend %global_flex_block_row_wrap_flex-start;
}

.gender__item {
	width: 50%;
}

.gender__elem {
	position: absolute;
	z-index: 1;

	width: 0.1px;
	height: 0.1px;

	opacity: 0;
	pointer-events: none;

	&:checked+.gender__label {
		color: $green_2;

		&::after {
			height: 2px;
			background-color: $orange;
		}
	}
}

.gender__link,
.gender__label {
	@extend %transition_color;

	position: relative;

	display: block;
	width: 100%;

	font-size: 1.4rem;
	font-weight: 300;
	line-height: 4.2rem;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	text-align: center;
	color: $gray_3;

	&::after {
		position: absolute;
		z-index: 1;
		bottom: -1px;
		left: 0;
		right: 0;

		height: 1px;

		background-color: $gray_2;
		content: "";
	}

	.gender__item--active_mod & {
		color: $green_2;

		&::after {
			height: 2px;
			background-color: $orange;
		}
	}

	&:focus,
	&:hover {
		text-decoration: none;
	}

	&:hover {
		color: $green_2;
	}
}