%form_select_default {
	@extend %transition_all;

	display: block;
	width: 100%;
	height: 3.5rem;
	padding: 5px 5px 5px 0;
	font-size: 1.4rem;
	line-height: 1.7;
	letter-spacing: 0.03em;
	font-family: $font_1;
	color: $green;
	box-shadow: inset 0 -1px 0 0 $input_border_color;
	border: none;
	border-radius: initial;
	background-clip: padding-box;

	transition: box-shadow 0.3s ease;

	&::-webkit-input-placeholder {
		color: $input_placeholder_color;
	}

	&::-moz-placeholder {
		color: $input_placeholder_color;
	}

	&:-ms-input-placeholder {
		color: $input_placeholder_color;
	}

	&:hover,
	&:focus {
		box-shadow: inset 0 -1px 0 0 $input_border_color_active;
	}
}