@import "ScssHelpers/_helpers_all.scss";

.similar {
	@include bp($point_2, min) {
		padding: 10rem 2rem 8.5rem;
	}

	@include bp($point_3) {
		border-top: 1px solid $gray_2;
	}

	@include bp($point_3, $point_4) {
		padding: 5rem 2rem 10rem;
	}

	@include bp($point_4 - 1) {
		padding: 7rem 2rem 10rem;
	}
}

.similar_in {
	max-width: 91.2rem;
}

.similar__title {
	font-family: $font_1;
	color: $green_2;
	font-weight: 500;
	font-size: 2.5rem;
	line-height: 2.5rem;
	text-transform: uppercase;
	text-align: center;

	@include bp($point_2, min) {
		margin: 0 0 4.5rem 0;
		font-size: 2.5rem;
		line-height: 2.5rem;
	}

	@include bp($point_3) {
		font-size: 2rem;
		line-height: 2rem;
	}

	@include bp($point_3, $point_6) {
		margin: 0 0 4rem 0;
	}

	@include bp($point_6 - 1) {
		margin: 0 0 3rem 0;
	}

	@include bp($point_3, $point_4) {
		font-size: 2rem;
		line-height: 2rem;
	}
}

.similar__slider_block {
	position: relative;

	@include bp($point_2, min) {
		padding: 0 4rem;
	}

	@include bp($point_3) {
		padding: 0;
	}
}

.similar_card {
	@include flex_block(column, nowrap, center, stretch);
}

.similar_card__head {
	position: relative;
}

.similar_card__image_block {
	position: relative;
	display: block;
	width: 100%;

	@include bp($point_2, min) {
		padding: 0 0 120.4% 0;
	}

	@include bp($point_3, $point_5) {
		padding: 0 0 128.4% 0;
	}

	@include bp($point_5 - 1) {
		padding: 0 0 136% 0;
	}
}

.similar_card__img {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.similar_card__favorite_btn {
	@extend %fav_btn;
	right: 1rem;
	bottom: 1rem;

	@include flex_block(row, nowrap, center, center);

	@include bp($point_4) {
		display: none;
	}
}

.similar_card__favorite_icon {
	width: 2rem;
	height: 2rem;
}

.similar_card__info {
	@include flex_block(column, nowrap, center, center);

	@include bp($point_6, min) {
		padding: 1.5rem 1.5rem;
	}

	@include bp($point_6 - 1) {
		padding: 2rem 1.5rem;
	}
}

.similar_card__title {
	font-weight: 500;
	text-align: center;
	text-transform: uppercase;

	@include bp($point_3, min) {
		margin: 0 0 0.2rem 0;
	}

	@include bp($point_6, min) {
		font-size: 1.8rem;
		line-height: 2.5rem;
	}

	@include bp($point_6 - 1) {
		font-size: 1.4rem;
		line-height: 2rem;
	}
}

.similar_card__category {
	font-family: $font_1;
	color: $green_2;
	font-weight: 300;
	text-align: center;
	text-transform: capitalize;

	@include bp($point_3, min) {
		margin: 0 0 0.7rem 0;
	}

	@include bp($point_3, $point_6) {
		margin: 0 0 0.4rem 0;
	}

	@include bp($point_6, min) {
		font-size: 1.6rem;
		line-height: 2.2rem;
	}

	@include bp($point_6 - 1) {
		font-size: 1.4rem;
		line-height: 2rem;
	}
}

.similar_card__price_block {
	@include flex_block(row, wrap, center, center);
}

.similar_card__price {
	font-weight: 800;
	text-align: center;
	text-transform: uppercase;

	@include bp($point_6, min) {
		font-size: 1.4rem;
		line-height: 1.9rem;
	}

	@include bp($point_6 - 1) {
		font-size: 1.3rem;
		line-height: 1.6rem;
	}
	&.similar_card__price--old_price_mod {
		position: relative;
		padding: 0 0.2rem;
		font-weight: 500;
		color: $gray_4;
		margin: 0 0.5rem 0 0;

		&::after {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			width: 100%;
			height: 0.2rem;
			background-color: $gray_4;
			transform: translate(-50%, -50%);
		}
	}

	&.similar_card__price--new_price_mod {
		color: $red;
	}
}

.similar__controls_btn {
	position: absolute;
	z-index: 2;

	pointer-events: initial;
	border-radius: 50%;
	color: $green;
	cursor: pointer;
	transform: translateY(-50%);

	@include flex_block(row, nowrap, center, center);

	@include bp($point_2, min) {
		width: 2.6rem;
		height: 3.9rem;
	}

	@include bp($point_3, $point_6) {
		width: 7rem;
		height: 7rem;
	}

	@include bp($point_6, min) {
		top: 38%;
	}

	@include bp($point_6 - 1) {
		top: 32.8%;
		width: 5rem;
		height: 5rem;
	}

	@include bp($point_3) {
		background-color: #fff;
	}

	&.similar__controls_btn--prev {
		@include bp($point_3, min) {
			left: -0.5rem;
		}

		@include bp($point_3 - 1) {
			left: -3rem;
		}
	}

	&.similar__controls_btn--next {
		@include bp($point_3, min) {
			right: -0.5rem;
		}

		@include bp($point_3 - 1) {
			right: -3rem;
		}
	}
}

.similar__controls_icon {
	display: block;

	@include bp($point_3, min) {
		width: 2.6rem;
		height: 3.9rem;
	}

	@include bp($point_3 - 1, $point_6) {
		width: 2.2rem;
		height: 3.4rem;
	}

	@include bp($point_6 - 1) {
		width: 1rem;
		height: 3rem;
	}

	.similar__controls_btn--prev & {
		transform: rotate(180deg);
	}
}
