@import "ScssHelpers/_helpers_all.scss";

.products_section {
	position: relative;

	@include bp($point_2, min) {
		padding: 1rem 0 5rem;
	}

	@include bp($point_3) {
		padding: 2rem 0 5rem;
	}

	@include bp($point_4) {
		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: -2rem;
			width: 105vw;
			height: 1px;

			background-color: $gray_2;
		}
	}
}

.products_section__in {
	max-width: 84rem;
}

.products_section__wrap {
	&:not(:last-child) {
		margin-bottom: 5rem;
	}
}

.products_section__list {
	margin: 0 -1.5rem -4rem 0;

	@include flex_block(row, wrap, flex-start, stretch);
}

.products_section__item {
	flex-shrink: 0;

	padding: 0 1.5rem 4rem 0;

	@include bp($point_5, min) {
		width: 33.333%;
	}

	@include bp($point_5 - 1) {
		width: 50%;
	}
}

.products_section__btn {
	@extend %global_flex_block_row_nowrap_center_center;
}

.products_section__link {
	@extend %btn_size_1;
	@extend %blue_btn_1;

	min-width: 25rem;
	text-align: center;
}
