@import "ScssHelpers/_helpers_all.scss";
@import 'ScssHelpers/extends/forms/_form_default_input.scss';

.address_form {
	padding: 6rem 5rem;
	width: 100%;

	@include bp($point_2, min) {
		padding: 6rem 5rem;
	}

	@include bp($point_3) {
		padding: 3rem 1.5rem;
	}
}
.address_form__wrapper {
	width: 100%;

	@include bp($point_2, min) {
		max-width: 25rem;
	}
}

.address_form__input_w {
	width: 100%;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}
}

.address_form__input {
	width: 100%;
}

.address_form__select {
	width: 100%;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}
}

.address_form__option_block_row {
	margin-bottom: 3rem;

	& .address_form__input_w {
		margin: 0;

		&:not(:last-child) {
			margin-right: 10px;
		}
	}

	@include flex_block(row, nowrap, flex-start);

}

.address_form__submit {
	@extend %transition_opacity;

	padding: 1.6rem 3rem;

	font-size: 1.6rem;
	font-weight: 500;
	letter-spacing: .01rem;
	text-align: center;
	text-transform: uppercase;

	color: $gray_2;

	background-color: $green_2;

	cursor: pointer;

	&:hover {
		opacity: .7;
	}

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-right: 2.8rem;
		}
	}

	@include bp($point_4 - 1) {
		width: 100%;
		margin-bottom: 3.6rem;
	}
}