@import "ScssHelpers/_helpers_all.scss";

.sidebar_acc {
	@extend %transition_opacity;

	max-height: 0;

	opacity: 0;
	pointer-events: none;

	font-weight: 300;

	&.sidebar_acc--active_state {
		max-height: none;
		opacity: 1;
		pointer-events: initial;
	}
}

.sidebar_acc__item {
	padding: 1.4rem 0;

	border-top: 1px solid $gray_2;

	&:last-child {
		border-bottom: 1px solid $gray_2;
	}
}

// ---------------------------------------------- head
.sidebar_acc__item_head {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	position: relative;

	cursor: pointer;

	transition: margin-bottom .3s ease;
	will-change: margin-bottom;

	.sidebar_acc__item--active-mod & {
		margin-bottom: 1.4rem;
	}
}

.sidebar_acc__item_title {
	@extend %transition_opacity;
	@extend %text_2;

	$size_elem: 1rem;

	position: relative;

	width: 100%;
	padding-right: calc($size_elem + 1.4rem);

	line-height: 1.5;
	text-transform: uppercase;
	color: $green_2;

	&::before,
	&::after {
		@extend %transition_transform;
		@extend %transition_background;

		position: absolute;
		right: 0;
		top: 50%;

		width: $size_elem;
		height: 1px;

		background: $green_2;
		content: "";
	}

	&::before {
		transform: translateY(-50%) rotate(90deg);
	}

	&::after {
		transform: translateY(-50%);
	}

	.sidebar_acc__item--active-mod & {
		&::before {
			transform: translateY(-50%) rotate(180deg);
		}

		&::before,
		&::after {
			background: $orange;
		}
	}

	&:hover {
		opacity: .6;
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

// ---------------------------------------------- body
.sidebar_acc__item_body {
	max-height: 0;
	opacity: 0;

	transition: all 0.25s ease-out;
	will-change: max-height;
	overflow: hidden;
	pointer-events: none;

	.sidebar_acc__item--active-mod & {
		opacity: 1;
		pointer-events: auto;
	}
}

.sidebar_acc__item_body_in {
	@extend %text_2;

	line-height: 1.5;

	@include bp($point_2, min) {
		padding: 1.6rem 0 1.4rem;
	}

	@include bp($point_3) {
		padding: 1rem 0 1rem;
	}
}

.sidebar_acc__link {
	@extend %transition_opacity;
	@extend %text_2;

	line-height: 1.7;
	color: $green_2;

	&:hover {
		opacity: .6;
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}
}