@import "ScssHelpers/_helpers_all.scss";

.brand {
	@include bp($point_2, min) {
		padding: 12rem var(--gl-indent);
	}

	@include bp($point_3, $point_4) {
		padding: 9.1rem var(--gl-indent);
	}

	@include bp($point_4 - 1) {
		padding: 2.4rem var(--gl-indent);
	}

	&.brand.brand--right_mod {
		@include bp($point_2, min) {
			padding-bottom: 10rem;
		}
	}
}

.brand_in {
	max-width: 119.6rem;
	.brand--width_mod & {
		max-width: 97.5rem;
	}
}

.brand__wrapper {
	.brand:not(.brand--right_mod) & {
		@include bp($point_4, min) {
			@include flex_block(row, nowrap, flex-start);
		}
	}

	.brand.brand--right_mod & {
		@include bp($point_4, min) {
			@include flex_block(row-reverse, nowrap, flex-end);
		}
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4rem;
		}

		@include bp($point_3, $point_4) {
			margin-bottom: 5.2rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 12.2rem;
		}
	}

	@include bp($point_4 - 1) {
		@include flex_block(column-reverse, nowrap, flex-start);
	}
}

.brand__img_w {
	display: block;
	height: 100%;

	.brand:not(.brand--right_mod) & {
		@include bp($point_2, min) {
			margin-right: 8.5rem;
		}

		@include bp($point_3, $point_4) {
			margin-right: 4.4rem;
		}
	}

	.brand.brand--right_mod & {
		@include bp($point_2, min) {
			margin-left: 8.5rem;
		}

		@include bp($point_3, $point_4) {
			margin-left: 5rem;
		}
	}

	.brand.brand--width_mod & {
		@include bp($point_4, min) {
			width: 26.1rem;
		}

		@include bp($point_4 - 1) {
			height: 35rem;
		}
	}

	@include bp($point_2, min) {
		width: 33.4%;
	}

	@include bp($point_3, $point_4) {
		width: 35.7%;
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.brand__img {
	@extend %cover_img;

	position: relative;
	z-index: 3;

	object-position: top;
}

.brand__content {
	&.brand__content--center_mod {
		text-align: center;
	}

	.brand:not(.brand--width_mod) & {
		padding-top: 3.5rem;

		@include bp($point_2, min) {
			width: 52.5%;
		}
	}

	.brand--width_mod & {
		@include bp($point_2, min) {
			width: 62.5%;
			padding-top: 6.8rem;
		}

		@include bp($point_3) {
			padding-top: 3.2rem;
		}
	}

	@include bp($point_3, $point_4) {
		width: 57.5%;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 5.1rem;

		text-align: center;
	}
}

.brand__title {
	font-weight: 800;
	text-transform: uppercase;

	color: $green_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.1rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.5rem;
		}
	}

	@include bp($point_2, min) {
		font-size: 2.5rem;
	}

	@include bp($point_3) {
		font-size: 2rem;
	}
}

.brand__descr {
	margin-left: 0.2rem;
	font-size: 1.4rem;
	line-height: 1.35;
	letter-spacing: 0.04em;
	color: $green_2;

	opacity: 0.7;

	.brand:not(.brand--width_mod) & {
		@include bp($point_4, min) {
			max-width: 62.7rem;
		}
	}
}

.brand__second_img_w {
	position: relative;

	display: block;
	width: 100%;
	height: 100%;

	.brand:not(.brand--right_mod) & {
		@include bp($point_4, min) {
			margin-left: auto;
		}

		@include bp($point_2, min) {
			margin-right: 22.9%;
		}

		@include bp($point_3, $point_4) {
			margin-right: 16.8%;
		}
	}

	.brand.brand--right_mod & {
		@include bp($point_4, min) {
			margin-right: auto;
		}
		@include bp($point_2, min) {
			margin-left: 17.6%;
		}

		@include bp($point_3, $point_4) {
			margin-left: 12.8%;
		}
	}

	@include bp($point_2, min) {
		max-width: 25rem;
	}

	@include bp($point_3, $point_4) {
		max-width: 21rem;
	}

	@include bp($point_4 - 1) {
		max-width: 25.5rem;
		margin: 0 auto;
	}
}

.brand__second_img {
	@extend %cover_img;
}

.brand__second_img_decor_w {
	position: absolute;

	display: block;

	&::before,
	&::after {
		content: "";
		position: absolute;
	}

	&:before {
		z-index: 3;

		height: 1px;
		background-color: $gray;
	}

	&:after {
		z-index: 1;

		border: 1px solid $gray;
		border-radius: 50%;
	}

	&.brand__second_img_decor_w--pos_mod_1 {
		@include bp($point_4, min) {
			bottom: -9.4rem;
			left: -10.2rem;

			height: 21.9rem;
			width: 27.4rem;
		}

		@include bp($point_4 - 1) {
			top: -4.6rem;
			left: 5.1rem;

			height: 11.7rem;
			width: 17rem;
			// transform: rotate(108deg);
		}

		&:before {
			@include bp($point_4, min) {
				top: 49.5%;
				left: -9%;
				width: 31.9rem;
				transform: rotate(31.5deg);
			}

			@include bp($point_4 - 1) {
				top: 50.5%;
				left: -9%;
				width: 20.1rem;
				transform: rotate(140deg);
			}
		}

		&::after {
			@include bp($point_4, min) {
				top: 16.5%;
				left: 15.5%;
				width: 12.3rem;
				height: 12.3rem;
			}

			@include bp($point_4 - 1) {
				top: 3.4%;
				left: 36.6%;
				width: 7.8rem;
				height: 8rem;
			}
		}
	}

	&.brand__second_img_decor_w--pos_mod_2 {
		@include bp($point_4, min) {
			bottom: -9.4rem;
			left: -10.2rem;

			height: 21.9rem;
			width: 27.4rem;
		}

		@include bp($point_4 - 1) {
			bottom: 0;
			right: 6%;
			height: 11.7rem;
			width: 17rem;
			// transform: rotate(108deg);
		}

		&:before {
			@include bp($point_4, min) {
				top: 49.5%;
				left: -9%;
				width: 31.9rem;
				transform: rotate(31.5deg);
			}

			@include bp($point_4 - 1) {
				bottom: 33%;
				right: -33%;
				width: 18rem;
				transform: rotate(140deg);
			}
		}

		&::after {
			@include bp($point_4, min) {
				top: 16.5%;
				left: 15.5%;
				width: 12.3rem;
				height: 12.3rem;
			}

			@include bp($point_4 - 1) {
				display: none;
			}
		}
	}

	@include bp($point_3, $point_4) {
		display: none;
	}
}
