@import "ScssHelpers/_helpers_all.scss";

.banner {
	@extend %banner_bg;

	@include flex_block(column, nowrap, center, center);

	@include bp($point_4, min) {
		min-height: 41.7vw;
	}

	@include bp($point_4) {
		min-height: 46rem;
	}

	@include bp($point_5 - 1) {
		display: none;
	}
}

.banner__bg {
	@extend %stretch;
	@extend %cover_img;
}

.banner__content {
	position: relative;
	z-index: 3;

	max-width: 63rem;
	margin: 0 auto;
}

.banner__subtitle,
.banner__title {
	color: $gray_2;
	text-align: center;
}

.banner__subtitle {
	@extend %h4;

	@include bp($point_5, min) {
		&:not(:last-child) {
			margin-bottom: 5px;
		}
	}

	@include bp($point_5 - 1) {
		&:not(:last-child) {
			margin-bottom: 1px;
		}
	}
}

.banner__title {
	@extend %h2;

	@include bp($point_5, min) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_5 - 1) {
		&:not(:last-child) {
			margin-bottom: 4.4rem;
		}
	}
}

.banner__text {
	@extend %text;

	text-align: center;
	letter-spacing: 0.03em;
	color: $white;
}
