@import "ScssHelpers/_helpers_all.scss";

.address_block__item {
	position: relative;

	&::before {
		content: '';
		position: absolute;
		bottom: 0;
		height: 1px;
		background-color: $gray_2;
		
		@include bp($point_5, min) {
			left: 0;

			width: 200%;
		}

		@include bp($point_5 - 1) {
			left: calc(-1*var(--gl-indent));

			width: calc(100% + 2*var(--gl-indent));
		}		
	}

	@include bp($point_2, min) {
		padding: 5rem .5rem 6.3rem 5rem;
	}

	@include bp($point_3) {
		padding: 2.5rem 1.5rem;
	}

	
	@include bp($point_6, min) {
		@include flex_block(row, nowrap, flex-start);	
	}
}

.address_block__text {
	max-width: 36.5rem;
	margin-right: 3rem;

	font-weight: 400;
	font-size: 1.4rem;
	line-height: 1.4;
	
	color: $green;
}

.address_block__link {
	@extend %transition_color;

	margin-left: auto;

	font-weight: 400;
	font-size: 1.4rem;
	letter-spacing: .03rem;

	color: $orange;

	&:hover {
		text-decoration: none;

		color: darken($orange, 15);
	}

	@include bp($point_6 - 1) {
		display: block;
		margin-top: 1.5rem;
	}
}
