@import './sidebar_acc';

.sidebar {
	max-width: 24.1rem;
	width: 100%;

	flex: 0 0 auto;

	overflow-x: hidden;
	overflow-y: auto;
	max-height: 100vh;

	&::-webkit-scrollbar {
		display: none;
	}
	-ms-overflow-style: none;
	scrollbar-width: none;

	@include bp($point_3) {
		display: none;
	}
}

// ---------------------------------------------- content
.sidebar__in {
	@extend %sidebar_padding;

	position: relative;

	width: 100%;
}