@import 'ScssHelpers/_helpers_all.scss';

.filter {
	padding: 1rem 0;

	@include bp($point_6 - 1) {
		padding-bottom: .5rem;
	}
}

.filter__in {
	margin-left: 0;

	@include bp($point_2, min) {
		max-width: 84rem;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

// ---------------------------------------------- buttons
.filter__buttons {
	@extend %global_flex_block_row_wrap_flex-start;

	margin: 0 -1rem -1rem 0;

	@include bp($point_2, min) {
		display: none;
	}
}

.filter__button_w {
	width: 50%;
	padding: 0 1rem 1rem 0;
}

.filter__btn_open {
	@extend %filter_button;
}

// ---------------------------------------------- filter form
.filter__form {
	@include bp($point_3) {
		display: none;
	}
}

// ---------------------------------------------- elements
.filter__form_elements {
	margin: 0 -.5rem -.5rem 0;

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start);
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.filter__form_element {
	@include bp($point_2, min) {
		position: relative;

		max-width: 19rem;
		width: 100%;
		padding: 0 .5rem .5rem 0;
	}

	@include bp($point_3) {
		border-bottom: 1px solid #eae9e8;
		padding: 1.4rem 0;
	}
}