@import 'ScssHelpers/_helpers_all.scss';

.form_element__btn_select {
	@extend %text_2;

	cursor: pointer;

	@include bp($point_2, min) {
		position: relative;

		width: 100%;
		min-height: 4rem;
		padding: 0 3rem 0 1.4rem;

		color: $green_2;
		text-align: start;
		border: 1px solid $gray_2;

		&::after {
			position: absolute;
			top: 50%;
			right: 0;

			width: 0;
			height: 0;
			margin-top: 1px;

			content: "";
			border-style: solid;
			border-width: 5.5px 4px 0;
			border-color: $green_2 transparent transparent transparent;

			transition: transform .3s ease;
			will-change: transform;

			transform: translate(-1.4rem, -50%);

			.form_element--active-mod & {
				transform: translate(-1.4rem, -50%) rotateX(180deg);
			}
		}
	}

	@include bp($point_3) {
		$size_elem: 1rem;
		font-weight: 700;

		position: relative;

		width: 100%;
		padding-right: calc($size_elem + .5rem);

		line-height: 1.5;
		text-align: start;
		text-transform: uppercase;
		color: $green_2;

		transition: opacity .3s ease;
		will-change: opacity;

		&::before,
		&::after {
			position: absolute;
			right: 0;
			top: 50%;

			width: $size_elem;
			height: 1px;

			background: $green_2;
			content: "";

			transition: transform .3s ease, background .3s ease;
			will-change: transform, background;
		}

		&::before {
			transform: translateY(-50%) rotate(90deg);
		}

		&::after {
			transform: translateY(-50%);
		}

		.form_element--active-mod & {
			&::before {
				transform: translateY(-50%) rotate(180deg);
			}

			&::before,
			&::after {
				background: $orange;
			}
		}

		&:hover {
			opacity: .6;
		}

		&:hover,
		&:focus {
			text-decoration: none;
		}
	}
}

// ---------------------------------------------- options
.form_element__options_w {
	max-height: 0;
	width: calc(100% - .5rem);

	color: #333333;
	background: $white;
	opacity: 0;
	overflow: hidden;
	pointer-events: none;

	transition: all .3s ease-out;
	will-change: height, opacity, transform, padding;

	.form_element--active-mod & {
		max-height: initial;
		opacity: 1;
		pointer-events: auto;
		overflow: visible;
	}

	@include bp($point_2, min) {
		position: absolute;
		z-index: 10;

		padding: 1rem .6rem 6rem .7rem;

		border-left: 1px solid $gray_2;
		border-right: 1px solid $gray_2;
	}

	@include bp($point_3, $point_6) {
		.form_element--active-mod & {
			padding: 1.6rem 0 0 0;
			margin: 0 0 1.1rem 0;
		}
	}

	@include bp($point_6 - 1) {
		.form_element--active-mod & {
			padding: 1.6rem 0 0 0;
			margin: 0 0 1.1rem 0;
		}
	}
}

.form_element__options {
	&:not(.form_element__options--link_mod) {
		@extend %global_flex_block_row_wrap_flex-start;

		margin: 0 -1.2rem -1.2rem 0;
	}
}

.form_element__option {
	max-width: 100%;
	padding: 0 1.2rem 1.2rem 0;
}

.form_element__link {
	@extend %transition_opacity;
	@extend %text_2;

	line-height: 1.5;
	color: $green_2;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	&:hover {
		opacity: .6;
	}
}

.form_element__checkbox {
	display: block;
}

.form_element__checkbox_elem {
	position: absolute;
	z-index: 1;

	width: .1px;
	height: .1px;

	opacity: 0;
	background-clip: padding-box;

	&:checked+.form_element__checkbox_label {
		box-shadow: 0 0 0 1px $green_2;
	}
}

.form_element__checkbox_label {
	@extend %global_flex_block_row_wrap_center_center;

	position: relative;

	display: block;
	min-width: 3.3rem;
	min-height: 3.3rem;
	height: 100%;
	padding: .8rem 1rem;

	font-size: 1.2rem;
	font-weight: 300;
	line-height: 1.4;
	text-align: center;
	text-transform: uppercase;
	color: $green_2;

	box-shadow: 0 0 0 1px $gray_2;
	overflow: hidden;
	cursor: pointer;

	&::after,
	&::before {
		display: none;
	}
}

.form_element__btn_submit {
	@extend %transition_background;
	@extend %text_2;

	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;

	padding: 0 1rem;

	line-height: 3.4rem;
	color: $white;

	background: $green_2;
	text-transform: uppercase;
	cursor: pointer;

	&:hover {
		background-color: $orange;
	}

	&:not(.form_element__options--link_mod &) {
		// &:last-child {
		// width: 100%;
		// }
	}

	&.form_element__options--link_mod {
		// padding-bottom: 1rem;
		// &:not(:last-child) {
		// }

		// &:last-child {
		// padding-bottom: 0;
		// }
	}

	@include bp($point_3) {
		display: none;
	}
}