@import "ScssHelpers/_helpers_all.scss";

.new_arrival {
	@include bp($point_2, min) {
		padding: 12rem 0 0;
	}

	@include bp($point_3, $point_4) {
		padding: 12.5rem 0 0;
	}

	@include bp($point_4 - 1) {
		padding: 10rem 0 0;
	}
}

.new_arrival__wrap {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, stretch);
	}
}

.new_arrival__column {
	position: relative;

	height: auto;

	background-color: $gray;

	&:nth-child(2n - 1) {
		@include bp($point_2, min) {
			flex-shrink: 0;
			width: 44.44%;
		}

		@include bp($point_4 - 1) {
			overflow: hidden;
		}
	}

	&:nth-child(2n - 2) {
		@include flex_block(column, nowrap, center, center);

		@include bp($point_2, min) {
			width: 55.56%;
		}

		@include bp($point_4, min) {
			padding: 5rem;
		}

		@include bp($point_4 - 1) {
			padding: 5rem 0;
		}
	}

	@include bp($point_4, min) {
		min-height: 70rem;
	}

	@include bp($point_4 - 1) {
		min-height: 60rem;
	}
}

.new_arrival_content {
	@extend %stretch;

	height: 100%;
	width: 100%;
	max-width: 46.8rem;

	@include bp($point_4, min) {
		padding: 15rem 5rem 5rem;
	}

	@include bp($point_4 - 1) {
		padding: 12.5rem 4rem 4rem;
	}

	@include flex_block(column, nowrap, flex-start, flex-start);
}

.new_arrival__bg_w {
	@include bp($point_2, min) {
		padding-bottom: max(70rem, 109.4%);
	}

	@include bp($point_3) {
		padding-bottom: max(60rem, 91.2%);
	}
}

.new_arrival__bg {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;

	@include bp($point_4 - 1) {
		transform: scale(1.29) translate(-27px, -3px);
	}
}

.new_arrival_content_w {
	position: relative;
}

.new_arrival_content__title {
	@extend %h1;

	color: $white;
	font-family: $font_2;

	&:not(:last-child) {
		margin-bottom: 1.5rem;
	}
}

.new_arrival_image__link {
	@extend %transition_opacity;

	position: relative;
	z-index: 1;

	text-align: center;

	&:hover {
		opacity: .5;
	}
}

.new_arrival_content__text {
	@extend %text;

	color: $white;

	&:not(:last-child) {
		margin-bottom: 3.4rem;
	}
}

.new_arrival_content__link {
	@extend %transition_color;

	font-weight: 400;
	font-size: 1.6rem;
	line-height: 1.38;

	letter-spacing: 0.03em;
	text-transform: uppercase;
	color: $gray_2;

	&:focus,
	&:hover {
		text-decoration: none;
		color: $gray_3;
	}
}

.new_arrival_image {
	@extend %global_flex_block_row_wrap_center_center;

	width: 100%;
	max-width: 60rem;
	height: 100%;

	margin: auto;

	background-color: $white;

	@include bp($point_4, min) {
		padding: 11rem 3rem;
	}

	@include bp($point_4 - 1) {
		padding: 9.2rem 3rem;
	}
}

.new_arrival_image__wrap {
	position: relative;

	width: 100%;

	padding: 1.4rem 3.5rem;

	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		padding-bottom: 100%;
	}

	@include flex_block(column, nowrap, flex-end);

	@include bp($point_4, min) {
		height: 38rem;
		max-width: 38rem;
	}

	@include bp($point_4 - 1) {
		height: 31.7rem;
		max-width: 31.7rem;
	}
}

.new_arrival_image__bg {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.new_arrival_image__text {
	@extend %text;

	position: relative;
	z-index: 1;

	color: $white;
	text-align: center;

	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 6;
	-webkit-box-orient: vertical;
}
