@import "ScssHelpers/_helpers_all.scss";

.filter_sort {
	position: relative;

	padding: 1.5rem 0 0.6rem 0;

	@include bp($point_3) {
		display: none;
	}
}

.filter_sort__in {
	@extend %global_flex_block_row_wrap_flex-start;

	max-width: 84rem;
	width: 100%;
}

.filter_sort__count {
	font-size: 1.4rem;
	font-weight: 300;
	line-height: 1.7;
	color: $gray_3;
}

.filter_sort__sort_box {
	display: inline-block;

	margin-left: auto;
}

.filter_sort__select_w {
	display: inline-block;

	position: relative;

}

.filter_sort__label {
	font-family: $font_1;
	font-size: 1.4rem;
	font-weight: 300;
	line-height: 1.7;
	letter-spacing: 0.02em;
	color: $gray_3;
}

.filter_sort__select {
	position: relative;

	width: 8.4rem;
	max-width: 100%;
	margin: 0;
	padding: 0 1.2rem 0 0.5rem;

	font-size: 1.4rem;
	font-weight: 300;
	line-height: 1.7;
	letter-spacing: 0.07em;
	color: $green_2;

	background-color: transparent;

	border: none;
	outline: none;
	cursor: pointer;
	
	@include appearance(none)
}

.filter_sort__select_arrow {
	position: absolute;
	top: 50%;
	right: 0;

	width: 8px;
	height: 5.5px;
	margin-top: 1px;

	transform: translate(0, -50%);
	pointer-events: none;
}

.filter_sort__option {
	display: block;
	padding: 0 10px;
}
