@import "ScssHelpers/_helpers_all.scss";

.product_f {
	@include bp($point_2, min) {
		padding: 0.8rem 2rem 3.7rem;
	}

	@include bp($point_3, $point_4) {
		padding: 0.8rem 0 3.7rem;
	}

	@include bp($point_4 - 1) {
		padding: 0.8rem 0 3.4rem;
	}
}

.product_f__breadcrumbs_wrap {
	max-width: 133.2rem;

	margin: 0 auto 2.2rem;

	@include bp($point_3, $point_4) {
		margin-bottom: 2.4rem;
		padding: 0 2rem;
	}

	@include bp($point_4 - 1) {
		padding: 0 1.4rem;
		margin-bottom: 0.6rem;
	}
}

.product_f_in {
	@include bp($point_2, min) {
		max-width: 84rem;

		@include flex_block(row, nowrap, flex-start, stretch);
	}

	@include bp($point_3) {
		@include flex_block(column, nowrap, flex-start, center);
	}
}

.product_f__menu {
	margin-bottom: 2.3rem;

	@include bp($point_3) {
		padding-left: 2rem;
	}
}

.product_f__col {
	flex-shrink: 0;

	@include bp($point_2, min) {
		&:nth-child(1) {
			padding: 0 3.5rem 0 0;
			width: 66.66%;
		}
		&:nth-child(2) {
			width: 33.33%;
		}
	}

	@include bp($point_3) {
		width: 100%;

		&:nth-child(1) {
			padding: 0 2rem;
			&:not(:last-child) {
				margin-bottom: 4.6rem;
			}
		}
	}
}

.product_f__col_part {
	@include bp($point_2, min) {
		padding: 0;
	}
	@include bp($point_3) {
		padding: 0 2rem;
		@include flex_block(column, nowrap, flex-start, center);
	}

	@include bp($point_4 - 1) {
		padding: 0 1.4rem;
	}

	&:nth-child(1) {
		@include bp($point_2, min) {
			margin: 0 0 2.3rem 0;
		}

		@include bp($point_3, $point_4) {
			margin: 0 0 1.6rem 0;
		}

		@include bp($point_4 - 1) {
			margin: 0 0 2rem 0;
		}
	}
}

.product_f__thumbnails {
	@include bp($point_2, min) {
		// position: absolute;
		// top: 0;
		// left: 0;
		width: 8.5rem;
		// height: 34rem;
		margin: 0 1rem 0 0;
	}

	@include bp($point_3) {
		position: relative;
		max-width: 100%;
	}

	@include bp($point_3, $point_4) {
		margin: 0 0 4.6rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 0 4.6rem 0;
	}
}

.product_f__thumbnail {
	// height: 100%;
	// overflow: auto;
}

.product_f__thumbnail_in {
	position: relative;
	width: 100%;

	cursor: pointer;

	@include bp($point_2, min) {
		padding: 0 0 124% 0;
	}

	@include bp($point_3, $point_4) {
		padding: 0 0 123.74% 0;
	}

	@include bp($point_4 - 1) {
		padding: 0 0 134.7% 0;
	}
}

.product_f__thumbnail_img {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.product_f__thumbnail_btn {
	@extend %fav_btn;

	@include flex_block(row, nowrap, center, center);

	&:focus,
	&:hover {
		background-color: darken($white, 7%);
	}

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		display: flex;
	}

	@include bp($point_4, min) {
		right: 2rem;
		bottom: 2rem;
	}

	@include bp($point_4 - 1) {
		right: 1rem;
		bottom: 1rem;
	}
}

.product_f__thumbnail_icon {
	width: 2rem;
	height: 2rem;
}

.product_f__preview {
	@include bp($point_2, min) {
		width: 42.8rem;
		margin: 0 3.5rem 0 0;
	}

	@include bp($point_3) {
		display: none;
	}
}

.product_f__preview_item {
	position: relative;
	display: none;
	width: 100%;
	padding: 0 0 121.5% 0;

	&.product_f__preview_item--active_state {
		display: block;
	}
}

.product_f__preview_img {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.product_f__preview_btn {
	@extend %fav_btn;

	right: 2rem;
	bottom: 2rem;
}

.product_f__preview_icon {
	width: 2rem;
	height: 2rem;
}

.product_f__title {
	font-weight: 500;
	color: $green_2;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 2.5rem;
		line-height: 3.1rem;
	}

	@include bp($point_3) {
		font-size: 2rem;
		line-height: 2rem;
		text-align: center;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 0.2rem;
	}
}

.product_f__category {
	display: block;

	font-weight: 300;
	line-height: 2rem;
	color: $green_2;
	text-transform: capitalize;

	@include bp($point_3) {
		text-align: center;
	}

	@include bp($point_2, min) {
		margin-bottom: 0.8rem;
	}

	@include bp($point_4, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 1rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 1rem;
		font-size: 1.4rem;
	}
}

.product_f__price_block {
	@include flex_block(row, wrap, flex-start, center);
}

.product_f__price {
	font-weight: 500;
	letter-spacing: 0.04em;
	text-align: center;
	text-transform: uppercase;

	@include bp($point_6, min) {
		font-size: 1.8rem;
		line-height: 2.2rem;
	}

	@include bp($point_6 - 1) {
		font-size: 1.3rem;
		line-height: 1.6rem;
	}
	&.product_f__price--old_price_mod {
		position: relative;
		padding: 0 0.2rem;
		margin: 0 0.5rem 0 0;
		font-weight: 500;
		color: $gray_4;

		&::after {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			width: 100%;
			height: 0.2rem;
			background-color: $gray_4;
			transform: translate(-50%, -50%);
		}
	}

	&.product_f__price--new_price_mod {
		color: $red;
	}
}

.product_form {
	@include bp($point_2, min) {
		margin: 0 0 2rem 0;
	}

	@include bp($point_3) {
		width: 100%;
		margin: 0 0 2rem 0;
	}
}

.product_form__radio_block {
	@include flex_block(column, nowrap, flex-start, stretch);

	@include bp($point_3) {
		align-items: center;
	}

	@include bp($point_4, min) {
		margin: 0 0 2rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 0 1.4rem 0;
	}

	&:nth-child(2) {
		@include bp($point_2, min) {
			margin: 0 0 1.5rem 0;
		}

		@include bp($point_3, $point_4) {
			margin: 0 0 1.4rem 0;
		}

		@include bp($point_4 - 1) {
			margin: 0 0 1.3rem 0;
		}
	}
}

.product_form__radio_title {
	font-weight: 400;
	font-size: 1.2rem;
	line-height: 2rem;
	color: $green_2;

	@include bp($point_3) {
		margin: 0 0 1rem 0;
	}
}

.product_form__radio_list {
	@include flex_block(row, nowrap, flex-start, stretch);

	@include bp($point_2, min) {
		margin-bottom: 0.7rem;
	}

	@include bp($point_3) {
		margin-bottom: 1rem;
	}
}

.product_form__radio_item {
	&.product_form__radio_item--square_mod:not(:last-child) {
		margin: 0 1rem 0 0;
	}

	&.product_form__radio_item--circle_mod:not(:last-child) {
		margin: 0 1.5rem 0 0;
	}
}

.product_form__radio_wrap {
	position: relative;
}

.product_form__radio_inner {
	@include flex_block(row, nowrap, center, center);
	cursor: pointer;
	.product_form__radio_item--square_mod & {
		width: 3.5rem;
		height: 3.5rem;

		font-weight: 400;
		font-size: 1.2rem;
		line-height: 2rem;
		color: $green_2;
		text-transform: uppercase;

		border-width: 1px;
		border-style: solid;
		border-color: $gray_2;
	}

	.product_form__radio_item--circle_mod & {
		width: 2rem;
		height: 2rem;

		border-width: 1px;
		border-style: solid;
		border-radius: 50%;
	}

	.product_form__radio_item--circle_mod &.product_form__radio_inner--color_1_mod {
		background-color: $white;
		border-color: $gray_2;
	}
	.product_form__radio_item--circle_mod &.product_form__radio_inner--color_2_mod {
		background-color: #ff0000;
		border-color: #ff0000;
	}
	.product_form__radio_item--circle_mod &.product_form__radio_inner--color_3_mod {
		background-color: #1931aa;
		border-color: #1931aa;
	}
	.product_form__radio_item--circle_mod &.product_form__radio_inner--color_4_mod {
		background-color: $black;
		border-color: $black;
	}
}

.product_form__radio_input {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;

	.product_form__radio_item--square_mod &:checked ~ .product_form__radio_inner {
		border-color: $green_2;
	}

	.product_form__radio_item--square_mod &:disabled ~ .product_form__radio_inner {
		cursor: initial;
		background-color: $gray_2;
		color: #cec6c4;
	}

	.product_form__radio_item--circle_mod &:checked ~ .product_form__radio_inner {
		box-shadow: 0 0 0 0.24rem #86b7fe;
	}
}

.product_form__radio_link {
	font-weight: 400;
	font-size: 1.2rem;
	line-height: 2rem;
	letter-spacing: 0.04em;

	text-decoration: underline;

	transition: color 0.3s ease;
	&:hover,
	&:focus {
		color: lighten($green_2, 5%);
		text-decoration: none;
	}
}

.product_form__buttons {
	@include flex_block(column, nowrap, flex-start, stretch);

	@include bp($point_3) {
		width: 100%;
	}
}

.product_form__button {
	@extend %btn_size_1;
	font-weight: 500;

	@include bp($point_2, min) {
		min-width: 28rem;
	}

	@include bp($point_3) {
		width: 100%;
	}

	&:nth-child(1) {
		@extend %transparent_btn_1;

		@include bp($point_3) {
			margin: 0 0 0.5rem 0;
		}
	}

	&:nth-child(2) {
		@extend %dark_btn_1;
	}

	@include st_btn();
}

.product_f__infolinks {
	@include flex_block(row, wrap, space-between, stretch);

	@include bp($point_2, min) {
		margin: 0 0 1.5rem 0;
	}

	@include bp($point_3) {
		width: 100%;
	}

	@include bp($point_3, $point_4) {
		margin: 0 0 2.7rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 0 1.4rem 0;
	}
}

.product_f__infolinks_link {
	font-weight: 400;
	line-height: 2.4rem;
	letter-spacing: 0.04em;
	color: $green_2;
	opacity: 0.3;
	transition: opacity 0.3s ease;

	&:hover {
		opacity: 1;
		text-decoration: none;
	}

	@include bp($point_4, min) {
		font-size: 1.4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.2rem;
	}
}

.product_f__info {
	@include bp($point_3) {
		width: 100%;
	}
	h4 {
		margin: 0 0 1.8rem 0;
		font-weight: 500;
		font-family: $font_1;
		font-size: 1.4rem;
		line-height: 1.7rem;
		color: $green_2;
		text-transform: uppercase;
	}

	p {
		font-weight: 400;
		font-size: 1.4rem;
		line-height: 2.4rem;
		color: $green_2;
	}
}
