@import "ScssHelpers/_helpers_all.scss";

.products_grid {
	width: 100%;

	@include bp($point_2, min) {
		padding: 0 var(--gl-indent);
	}

	@include bp($point_3) {
		padding: 0.2rem var(--gl-indent);
	}
}

.products_grid__in {
	max-width: 134rem;
}

.products_grid__inner {
	background-color: $white;

	@include bp($point_2, min) {
		padding: 0.62rem var(--gl-indent) 1.3rem 0;
	}

	@include bp($point_3, $point_4) {
		padding: 0.62rem var(--gl-indent) 1.9rem 0;
	}

	@include bp($point_4 - 1) {
		padding: 0.4rem var(--gl-indent);
	}
}

.products_grid__wrap {
	@extend %global_flex_block_row_nowrap_flex-start;

	width: 100%;
}

.products_grid__col {
	@include bp($point_2, min) {
		margin-top: 0.6rem;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.products_grid__title {
	font-weight: 300;
	line-height: 1.4;
	text-transform: uppercase;
	text-align: start;
	color: $green_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3rem;
		}

		@include bp($point_3, $point_6) {
			margin-bottom: 2rem;
		}

		@include bp($point_6 - 1) {
			margin-bottom: 1.5rem;
		}
	}

	@include bp($point_2, min) {
		font-size: 2.5rem;
	}

	@include bp($point_3) {
		font-size: 2rem;
	}
}
