@import "ScssHelpers/_helpers_all.scss";

.bottom_banner {
	@extend %global_flex_block_column_nowrap_center_center;

	background-color: $gray_2;

	min-height: 27.5rem;

	@include bp($point_4 - 1) {
		padding-top: 5rem;
		padding-bottom: 5rem;
	}
}

.bottom_banner__title {
	font-weight: 700;
	font-size: 2rem;
	line-height: 1.335;
	text-align: center;
	text-transform: uppercase;

	color: $green_2;

	&:not(:last-child) {
		margin-bottom: 2.5rem;
	}
}

.bottom_banner__text {
	@extend %text;

	max-width: 58rem;
	margin: 0 auto;

	color: $green_2;
	text-align: center;
	opacity: 0.7;
	letter-spacing: 0.03em;
}
