@import "ScssHelpers/_helpers_all.scss";

.slider_section {
	@include bp($point_2, min) {
		padding-bottom: 0;
		padding-top: 7.8rem;
	}

	@include bp($point_3, $point_4) {
		padding-top: 0;
	}
}

.slider_section__wrap {
	max-width: 63rem;
	margin: 0 auto 5rem;
}

.slider_section__subtitle {
	@extend %h4;

	color: $green_2;
	text-align: center;
	font-family: $font_2;

	&:not(:last-child) {
		margin-bottom: 0.5rem;
	}
}

.slider_section__title {
	@extend %h2;

	text-align: center;
	color: $green_2;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}
}

.slider_section__text {
	@extend %text;

	text-align: center;
	letter-spacing: 0.03em;
	color: $green_2;
}

.slider_section__inner {
	position: relative;
	width: 100vw;

	@include bp($point_2, min) {
		margin-left: -5rem;
	}

	@include bp($point_3, $point_4) {
		margin-left: -2rem;
	}

	@include bp($point_4 - 1) {
		margin-left: -1.5rem;
	}
}

.slider_section__item {
	@include bp($point_4, min) {
		padding-bottom: 41.67%;
	}

	@include bp($point_4, $point_6) {
		padding-bottom: 60%;
	}

	@include bp($point_6 - 1) {
		padding-bottom: 100%;
	}
}

.slider_section__img {
	@extend %stretch;
	@extend %cover_img;

	object-position: top center;
}

.slider_section__controls {
	@include bp($point_4 - 1) {
		display: none;
	}
}

.slider_section__arrow {
	@extend %global_flex_block_row_wrap_center_center;
	@extend %transition_opacity;

	position: absolute;
	z-index: 2;
	top: 50%;

	background-color: $white;
	border-radius: 50%;
	opacity: 0.5;
	cursor: pointer;

	transform: translateY(-50%);

	@include bp($point_2, min) {
		width: 9rem;
		height: 9rem;
		padding: 2.5rem 3rem;
	}

	@include bp($point_3, $point_4 + 1) {
		width: 7rem;
		height: 7rem;
		padding: 1.8rem 2rem;
	}

	@include bp($point_4) {
		width: 5rem;
		height: 5rem;
		padding: 1rem 1.5rem;
	}

	&:focus,
	&:hover {
		opacity: 1;
	}

	&.team_section_slider__arrow--prev_mod {
		left: 4rem;

		& .icon {
			transform: rotate(180deg);
		}
	}

	&.team_section_slider__arrow--next_mod {
		right: 4rem;
	}
}
