@import "ScssHelpers/_helpers_all.scss";

.popular {
	@include bp($point_2, min) {
		padding-top: 12rem;
		padding-bottom: 8rem;
	}
	@include bp($point_3, $point_4) {
		padding: 9.8rem 2rem 3.6rem;
	}
	@include bp($point_4 - 1) {
		padding-top: 7rem;
		padding-bottom: 5rem;
	}
}

.popular_in {
	max-width: 105.5rem;
}

.popular__title {
	font-family: $font_2;
	font-size: 3rem;
	font-weight: 300;
	letter-spacing: 0.05em;
	text-transform: uppercase;
	text-align: center;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 7.3rem;
		}
		@include bp($point_3, $point_4) {
			margin-bottom: 3.8rem;
		}
		@include bp($point_4 - 1) {
			margin-bottom: 2.7rem;
		}
	}
}

.popular__slider_block {
	position: relative;

	@include bp($point_2, min) {
		margin-bottom: 9.1rem;
	}
	@include bp($point_3, $point_4) {
		margin-bottom: 5.8rem;
	}
	@include bp($point_4 - 1) {
		margin-bottom: 5.1rem;
	}
}

.popular__slider {
	width: 100%;
}

.popular__slider_item {
	height: auto !important;
}

.popular__card {
	display: block;
	text-align: center;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.popular__card_picture {
	position: relative;
	display: block;

	@include bp($point_2, min) {
		margin-bottom: 3.2rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 1.7rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 2rem;
	}

	&::before {
		content: "";
		display: block;

		@include bp($point_2, min) {
			padding-top: 158%;
		}

		@include bp($point_3, $point_4) {
			padding-top: 130%;
		}

		@include bp($point_4 - 1) {
			padding-top: 133.6%;
		}
	}
}

.popular__card_img {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.popular__card_info {
	font-family: $font_3;
}

.popular__card_info_title {
	font-weight: 500;
	text-transform: uppercase;
	color: $green;

	@include bp($point_3, $point_4) {
		margin-bottom: -.2rem;
	}

	@include bp($point_4, min) {
		font-size: 1.8rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
	}
}

.popular__card_info_subtitle {
	font-weight: 300;

	@include bp($point_2, min) {
		margin-bottom: .8rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: .6rem;
	}

	@include bp($point_4, min) {
		font-size: 1.6rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: .2rem;
		font-size: 1.4rem;
	}
}

.popular__card_info_price {
	@extend %global_flex_block_row_wrap_center_center;

	font-size: 1.4rem;
	font-weight: 500;
	letter-spacing: .04em;

	@include bp($point_4 - 1) {
		font-size: 1.3rem;
	}
}

.popular__card_info_old_price {
	position: relative;
	margin-right: .6rem;


	color: $gray_4;

	&::before {
		position: absolute;
		top: 0.8rem;
		left: -0.4rem;

		width: 109%;
		height: 2px;

		content: "";

		background-color: $gray_4;
	}
}

.popular__card_info_current_price {
	color: $red;
}

.popular__slider_controls {
}

.popular__slider_controls_btn {
	@extend %global_flex_block_row_wrap_center_center;

	position: absolute;
	z-index: 2;

	background-color: $white;
	border-radius: 50%;
	cursor: pointer;

	transform: translateY(-50%);

	@include bp($point_2, min) {
		top: 42.5%;

		width: 9rem;
		height: 9rem;
	}
	@include bp($point_3, $point_4) {
		top: 39%;

		width: 7rem;
		height: 7rem;
	}
	@include bp($point_4 - 1) {
		top: 50%;

		width: 5rem;
		height: 5rem;

		transform: translateY(-130%);
	}

	&.popular__slider_controls_btn--prev {
		@include bp($point_2, min) {
			left: -4.5rem;
		}	
		@include bp($point_3, $point_4) {
			left: -3rem;
		}
		@include bp($point_4 - 1) {
			left: -2.3rem;
		}

		& .icon {
			transform: rotate(180deg);
		}
	}

	&.popular__slider_controls_btn--next {
		@include bp($point_2, min) {
			right: -4.5rem;
		}
		@include bp($point_3, $point_4) {
			right: -3rem;
		}
		@include bp($point_4 - 1) {
			right: -2.3rem;
		}
	}
}

.slider_controls__icon {
	color: $black;

	@include bp($point_2, min) {
		width: 1.8rem;
		height: 4.2rem;
	}
	@include bp($point_3, $point_4) {
		width: 1.2rem;
		height: 3rem;
	}
	@include bp($point_4 - 1) {
		width: 1.1rem;
		height: 2.2rem;
	}
}

.popular__bottom_decor {
	@include flex_block(row, wrap, center, flex-start, flex-start);
}

.popular__bottom_decor_img_w {
	position: relative;

	@include bp($point_2, min) {
		width: 11.5rem;
		height: 1.4rem;
	}
	@include bp($point_3) {
		width: 9.6rem;
		height: 1.2rem;
	}

	&::before,
	&::after {
		position: absolute; 
		top: 50%; 
		right: 122%;
	
		width: 1000rem; 
		height: 1px; 
		content: '';

		background-color: #EAE9E8;
	}

	&::after {
		right: unset;
		left: 122%;
	 }
}

.popular__bottom_decor_img {
	@extend %contain_img;
}
