@import "ScssHelpers/_helpers_all.scss";

.account_nav {
	width: 100%;
	position: relative;
	
	@include bp($point_2, min) {
		max-width: 23.7rem;
		border-left: 1px solid $gray_2;
		border-right: 1px solid $gray_2;
	}
	
	@include bp($point_3, $point_5) {
		max-width: 23.8rem;
		border-right: 1px solid $gray_2;
	}

	@include bp($point_5 - 1) {
		max-width: 100%;
		border: 1px solid $gray_2;
		margin: 0 0 2.4rem 0;
	}
}

.account_nav__list {
	@include bp($point_2, min) {
		padding: 1.5rem 1.7rem;
	}

	@include bp($point_3, $point_5) {
		padding: 1.5rem 2rem;
	}

	@include bp($point_5 - 1) {
		position: absolute;
		top: 100%;
		left: -1px;
		z-index: 3;
		width: calc(100% + 2px);
		padding: 0;
		background-color: $white;
		border: 1px solid $gray_2;
		overflow: auto;
	}

	&.account_nav__list--visible_state {
		@include bp($point_5 - 1) {
			display: block;
		}
	}

	@include bp($point_5 - 1) {
		display: none;
	}
}

.account_nav__item {
	position: relative;

	@include bp($point_5, min) {
		color: $green_2;
	}

	@include bp($point_5 - 1) {
		color: $gray_3;
	}

	&:not(:last-child) {

		@include bp($point_5, min) {
			margin: 0 0 3rem 0;
		}

		@include bp($point_5 - 1) {
			margin: 0;
		}
	}

	&.account_nav__item--active_mod,
	&:hover {

		@include bp($point_5, min) {
			color: $orange;
		}

		@include bp($point_5 - 1) {
			color: $gray_3;
		}
	}

	&.account_nav__item--active_mod::before {
		@extend %transition_background;
	
		content: '';
		position: absolute;
		left: -1.8rem;
		top: 50%;
		width: 2px;
		height: 5rem;
		background-color: $orange;
		transform: translateY(-50%);
	
		@include bp($point_3) {
			display: none;
		}
	}
}

.account_nav__link {
	@include flex_block(row, wrap, flex-start);
	color: currentColor;

	@include bp($point_5 - 1) {
		padding: 1.5rem 1rem;
	}

	&:hover {
		text-decoration: none;
	}
}

.account_nav__icon {
	@extend %transition_color;

	width: 1.9rem;
	height: 1.9rem;
	color: currentColor;
	@include bp($point_5, min) {
		margin-right: 1.3rem;
	}
	
	@include bp($point_5 - 1) {
		margin-right: 1rem;
	}

}

.account_nav__title {
	@extend %transition_color;

	font-style: normal;
	font-weight: 800;
	font-size: 1.4rem;

	color: currentColor;
	
}

.account_nav__current {
	padding: 1rem 1rem 1rem 0.8rem;
	color: $green_2;
	@include bp($point_5, min) {
		display: none;
	}

	@include bp($point_5 - 1) {
		@include flex_block(row, nowrap, flex-start, center);
	}
}

.account_nav__current_triangle {
	margin: 0 0 0 auto;
	width: 0;
	height: 0;
	border-style: solid;
	border-width: .5rem .3rem 0 .3rem;
	border-color: $green_2 transparent transparent transparent;
}